.customButton {
  outline: none;
  background-color: gray;
  color: white;
  padding: 10px 30px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-family: sans-serif;
  font-size: 1rem;
  border-radius: 5px;
  margin-top: 2px;
  border: 1px solid rgba(255, 255, 255, 0);
}
.customButton:hover {
  border: 1px solid black;
}
.customButtonPrimary {
  background-color: #00b5ad;
}
.customButtonPrimary:hover {
  border: 1px solid #004a43;
}
.customButtonBasic {
  background-color: rgba(0, 255, 248, 0);
  border: 1px solid #979797;
  color: #424242;
}
.customButtonBasic:hover {
  color: #232323;
}
.UIGroup {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
}
