@import "../../_variables.less";

.clusterObject {
  display: flex;
  position: absolute;
  flex-direction: column;
  border: @itemsBorder;
  border-radius: @itemBorderRadius;
  min-height: 60px;
  padding: 10px;
  min-width: 90px;
  align-items: center;
}

.selectedCluster {
  border: @selectedItemBorder;
  background-color: @selectedItemColor;
}
.highlightedCluster{
  border: 1px solid grey;
}
.overlayCluster{
  //border: 1px solid grey;
  border: @selectedItemBorder;
  background-color: @selectedItemColor;
}
.clusterObject:hover {
  //box-shadow: 1px 1px 3px black;
}

.imagesContainer {
  display: flex;
  position: relative;
  width: 100%;
  height: 45px;
  justify-content: center;
  align-items: center;
}

.previewClusterImage {
  position: absolute;
  width: 40px;
  height: 40px;
  float: left;
  background-color: grey;
  border-radius: 50%;
  border: 1px solid black;
}

