.clusterLine{
  display: grid;
  grid-template-columns: 25px 4fr 2fr 1fr;
  align-items: center;
  border-radius: 6px;
  margin-bottom: 7px;
  margin-top: 0px;
  //border: 0.5px solid #c8c8c8;
  box-shadow: 0 0 5px rgba(0,0,0,0.3);
}

.selectedClusterLine{
  //background-color: #d3d7d6;
  border: 2px solid #a9a9a9;
}

.highlightedClusterLine{
  background-color: rgba(39, 79, 169, 0.16);
}

.clusterName{
  font-family: sans-serif;
  font-size: 11pt;
}

